@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500);

*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}

:root {
  /* colors */
  --body-bg-color: #eee;
  --sidebar-bg-color: #f4f4f4;
  --text-color: #333;
  --title-color: #111;
  --date-color: #1f94c2;
  --dark-body-bg-color: #242424;
  --dark-sidebar-bg-color: #171717;
  --dark-text-color: #d8d8d8;
  --dark-title-color: #eee;
  --dark-date-color: #e6ef71;

  --icon-color: var(--text-color);
  --circle-color: #aaa;
  --circle-color-plain: #555;
  --dark-icon-color: var(--dark-text-color);
  --dark-circle-color: #6c6c6c;
  --dark-circle-color-plain: #eee;

  /* fonts */
  --body-font: "Poppins", "Sans-serif";
  --h1-font-size: 1.7rem;
  --h2-font-size: 1.3rem;
  --h3-font-size: 1.1rem;
  --h4-font-size: 1rem;

  /* helpers */
  --spacing-1: 0.4rem;
  --spacing-2: 0.8rem;
  --spacing-3: 1.2rem;
  --spacing-4: 1.5rem;
  --spacing-5: 2rem;

  --transition-duration-mode: 0.7s;
}

/* Transition pour changement de mode */
* {
  transition: color 0.7s ease-in-out;
  transition: color var(--transition-duration-mode) ease-in-out;
  transition: background-color 0.7s ease-in-out;
  transition: background-color var(--transition-duration-mode) ease-in-out;
}
body {
  font-family: "Poppins", "Sans-serif";
  font-family: var(--body-font);
  font-weight: 200;
  color: #333;
  color: var(--text-color);
  background-color: #eee;
  background-color: var(--body-bg-color);
}
.dark {
  color: #d8d8d8 !important;
  color: var(--dark-text-color) !important;
  background-color: #242424;
  background-color: var(--dark-body-bg-color);
}
h1,
h2,
h3 {
  margin-bottom: 0.4rem;
  margin-bottom: var(--spacing-1);
  font-weight: 400;
  color: #111;
  color: var(--title-color);
}
.dark h1,
.dark h2,
.dark h3 {
  color: #eee;
  color: var(--dark-title-color);
}
h1 {
  font-size: 1.7rem;
  font-size: var(--h1-font-size);
  text-transform: uppercase;
}
h2 {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 1.5rem;
  margin-bottom: var(--spacing-4);
  font-size: 1.3rem;
  font-size: var(--h2-font-size);
  text-transform: uppercase;
  letter-spacing: 4px;
}
h3 {
  font-size: 1.1rem;
  font-size: var(--h3-font-size);
}
button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.dark button {
  color: #d8d8d8;
  color: var(--dark-text-color);
}

a {
  text-decoration: none;
  color: #333;
  color: var(--text-color);
}
.dark a {
  color: #d8d8d8;
  color: var(--dark-text-color);
}
p {
  margin-bottom: 0.8rem;
  margin-bottom: var(--spacing-2);
}
ul {
  padding-left: 1.3rem;
}
li {
  margin-bottom: 0.8rem;
  margin-bottom: var(--spacing-2);
}

/* helpers */
.mb3 {
  margin-bottom: 1.2rem;
  margin-bottom: var(--spacing-3);
}
.mb5 {
  margin-bottom: 2rem;
  margin-bottom: var(--spacing-5);
}

/* grids */
.grid__row {
  margin-bottom: 1.2rem;
  margin-bottom: var(--spacing-3);
}
@media screen and (min-width: 768px) {
  .grid__container {
    display: grid;
    grid-template-columns: 1fr 3fr;
  }
  .grid__row {
    display: grid;
    grid-template-columns: 1fr 2fr;
    margin: 0;
  }
}

/* layouts */
.sidebar {
  padding: 1.5rem;
  background-color: #f4f4f4;
  background-color: var(--sidebar-bg-color);
}
