  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  content: "";
  display: block;
  width: 100%;
  height: 120px;
  background-color: #bae5ff;
} */
.dark .sidebar {
  padding: 1.5rem;
  background-color: #171717;
  background-color: var(--dark-sidebar-bg-color);
}
.actions {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.main {
  padding: 1.5rem;
}

/* icons */
.circle {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 8px;
  border-radius: 50%;
  background-color: #aaa;
  background-color: var(--circle-color);
}
.circle-plain {
  background-color: #555;
  background-color: var(--circle-color-plain);
}
.dark .circle {
  background-color: #6c6c6c;
  background-color: var(--dark-circle-color);
}
.dark .circle-plain {
  background-color: #eee;
  background-color: var(--dark-circle-color-plain);
}

/* Generate PDF */

#cv-print {
  width: 100% !important;
}
.cv-print .actions {
  display: none;
}
.cv-print ul {
  margin: 0;
  padding: 0;
}
.cv-print ul li {
  list-style-type: none;
}
.cv-print ul li:before {
  content: "â€¢";
  display: inline-block;
  font-size: 110%;
  padding-right: 10px;
}
.cv-print .grid__container {
  font-size: 12px !important;
  max-width: 210mm;
}
.cv-print .sidebar {
  height: 297mm;
}
.cv-print .user__infos svg {
  display: none !important;
}
.cv-print .circle {
  width: 10px;
  height: 10px;
}
.cv-print .grid__date {
  margin-top: 5px;
}

.user {
    position: relative;
    text-align: center;
}
.user__avatar {
    border-radius: 50%;
}
.user__infos {
    text-align: left;
    padding: var(--spacing-3) 0 var(--spacing-1) 0;
}
.user__info {
    display: -webkit-flex;
    display: flex;
}
.user__info svg {
    color:var(--icon-color);
    margin-right: 1rem;
}
.dark .user__info svg {
    color:var(--dark-icon-color);
}
.skills {
    margin:var(--spacing-3) 0;
}
.skill {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}
.interests {
    /* display:none; */
    -webkit-align-items: center;
            align-items: center;
}
.interests svg {
    margin: var(--spacing-1);
}
.grid__item,
.grid__item p {
  margin-bottom: var(--spacing-1);
}
.grid__date {
  font-weight: 500;
}
.grid__title {
  margin-bottom: 0;
}
.grid__location {
  font-weight: 300;
  color: var(--date-color);
}
.dark .grid__location {
  color: var(--dark-date-color);
}
.grid__mission {
  margin-bottom: var(--spacing-1);
}
